html {
  font-size: 16px;
  font-family: "Hanken Grotesk", sans-serif;
}

img {
  width: 100%;
  height: auto;
}

header {
  display: flex;
  height: 70px;
  justify-content: center;
  align-items: center;
  background-color: hsl(224, 30%, 27%);
}
header h1 {
  font-family: "Hanken Grotesk";
  text-align: center;
  color: hsl(0, 0%, 95%);
  margin: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  background-color: hsl(0, 0%, 95%);
}

.container {
  margin: auto;
}

footer {
  display: flex;
  height: 70px;
  min-height: 70px;
  justify-content: center;
  align-items: center;
  background-color: hsl(224, 30%, 27%);
  font-family: "Hanken Grotesk";
  text-align: center;
  color: hsl(0, 0%, 95%);
}
footer .attribution {
  font-size: 11px;
  text-align: center;
}
footer .attribution a {
  color: hsl(0, 0%, 95%);
  text-decoration: none;
  font-weight: bold;
}
footer .attribution a:hover {
  text-decoration: underline;
}